* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

ul {
  list-style: none; }

a,
a:hover {
  text-decoration: none; }

input,
button,
textarea,
select {
  outline: none; }

a,
button {
  cursor: pointer;
  border: none;
  color: #fff; }

body {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
  background-color: #EEEEEE;
  font-size: 16px; }

.title {
  font-family: 'Oswald', sans-serif;
  font-weight: 700;
  font-size: 3rem;
  line-height: 4rem;
  text-align: center;
  color: #fff;
  margin-bottom: 2.625rem; }

.subtitle {
  font-size: 1.5rem;
  line-height: 2rem;
  text-align: center;
  color: #fff;
  max-width: 26.625rem;
  margin: 0 auto; }

.container {
  padding: 0 20px;
  max-width: 58.75rem;
  margin: 0 auto;
  position: relative; }

#nav-menu-toggle {
  display: none; }

.nav {
  margin-bottom: 13.063rem; }
  .nav__container {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .nav__logo {
    font-size: 1.25rem;
    font-weight: 600;
    line-height: 1rem;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    align-self: flex-start;
    transition: all 0.6s linear;
    z-index: 4; }
    .nav__logo:hover {
      transform: translate(10px); }
  .nav-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto; }
    .nav-menu__item {
      padding: 0 1rem; }
      .nav-menu__item:hover .nav-menu__link::after,
      .nav-menu__item--active .nav-menu__link::after {
        visibility: visible;
        transform: scale(1, 1);
        transform-origin: right; }
    .nav-menu__link {
      font-size: 0.875rem;
      line-height: 1rem;
      letter-spacing: 0.2em;
      display: inline-block; }
      .nav-menu__link::after {
        content: '';
        display: inline-block;
        height: 2px;
        width: 100%;
        background-color: #fff;
        transform: scale(0, 1);
        transform-origin: left;
        transition: transform 500ms ease;
        visibility: hidden; }

@media screen and (max-width: 768px) {
  #nav-menu-toggle:checked ~ .nav-menu__burger > span {
    transform: rotate(45deg); }
  #nav-menu-toggle:checked ~ .nav-menu__burger > span::before {
    top: 0;
    transform: rotate(0); }
  #nav-menu-toggle:checked ~ .nav-menu__burger > span::after {
    top: 0;
    transform: rotate(90deg); }
  #nav-menu-toggle:checked ~ .nav-menu {
    visibility: visible; } }

@media screen and (max-width: 768px) {
  .nav-menu__burger span,
  .nav-menu__burger span::before,
  .nav-menu__burger span::after {
    content: '';
    transition: 0.2s ease;
    display: block;
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #fff;
    border-radius: 0.313rem; }
  .nav-menu {
    position: fixed;
    top: 0;
    left: 0;
    visibility: hidden;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    text-align: center;
    background-color: #00b7ff;
    z-index: 3; }
    .nav-menu__item {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0;
      padding: 0.5rem 0; }
      .nav-menu__item:hover .nav-menu__link::after,
      .nav-menu__item--active .nav-menu__link::after {
        visibility: hidden; }
    .nav-menu__burger {
      width: 1.875rem;
      z-index: 4;
      position: relative; }
      .nav-menu__burger span,
      .nav-menu__burger span::before,
      .nav-menu__burger span::after {
        top: 40%; }
      .nav-menu__burger span::before {
        top: -8px; }
      .nav-menu__burger span::after {
        top: 8px; } }

.header {
