@keyframes spin {
  form {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

@media screen and (max-width: 768px) {
  .footer {
    padding-top: 3.625rem; }
  .footer-row {
    display: flex;
    justify-content: center;
    align-items: center;
